/* ///////////////////////////////burger */
.burger-wrap {
  position: relative;
  width: 25px;
  height: 15px; }
  .burger-wrap svg {
    height: 80px;
    position: absolute;
    width: 80px;
    top: -30px;
    left: 0; }
  .burger-wrap .plates {
    display: flex;
    flex-wrap: wrap; }
  .burger-wrap .burger {
    filter: url(#gooeyness); }
  .burger-wrap .x {
    transform: scale(0);
    transition: transform 400ms; }
  .burger-wrap .line {
    fill: none;
    stroke: #fff;
    stroke-width: 6px;
    stroke-linecap: round;
    stroke-linejoin: round;
    transform-origin: 50%;
    transition: stroke-dasharray 500ms 200ms, stroke-dashoffset 500ms 200ms, transform 500ms 200ms; }
  .burger-wrap .x .line {
    stroke-width: 5.5px; }
  .burger-wrap .plate7 .line {
    transition: stroke-dasharray 400ms 100ms, stroke-dashoffset 400ms 100ms, transform 400ms 100ms; }
  .burger-wrap .plate7 .line1 {
    stroke-dasharray: 21 102; }
  .burger-wrap .plate7 .line2 {
    stroke-dasharray: 21 92; }
  .burger-wrap .plate7 .line3 {
    stroke-dasharray: 21 97; }
  .burger-wrap .plate7 .line4 {
    stroke-dasharray: 21 97; }
  .burger-wrap .plate7 .line5 {
    stroke-dasharray: 21 92; }
  .burger-wrap .plate7 .line6 {
    stroke-dasharray: 21 102; }
  .burger-wrap .plate7 .x {
    transition: transform 400ms 50ms; }
  .burger-wrap .active.plate7 .line {
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms, transform 400ms; }
  .burger-wrap .active.plate7 .line1 {
    stroke-dasharray: 2 102;
    stroke-dashoffset: -102px; }
  .burger-wrap .active.plate7 .line2 {
    stroke-dasharray: 2 92;
    stroke-dashoffset: -92px; }
  .burger-wrap .active.plate7 .line3 {
    stroke-dasharray: 2 97;
    stroke-dashoffset: -97px;
    transition-delay: 20ms; }
  .burger-wrap .active.plate7 .line4 {
    stroke-dasharray: 2 97;
    stroke-dashoffset: -97px;
    transition-delay: 20ms; }
  .burger-wrap .active.plate7 .line5 {
    stroke-dasharray: 2 92;
    stroke-dashoffset: -90px;
    transition-delay: 40ms; }
  .burger-wrap .active.plate7 .line6 {
    stroke-dasharray: 2 102;
    stroke-dashoffset: -100px;
    transition-delay: 40ms; }
  .burger-wrap .active.plate7 .x {
    transition: transform 400ms 50ms;
    transform: scale(1); }

